@import url(https://fonts.googleapis.com/css?family=Sedgwick+Ave+Display&display=swap);
@import url(https://fonts.googleapis.com/css?family=Sedgwick+Ave+Display&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: -webkit-gradient(linear, left bottom, right top, from(#ffd8ee), color-stop(#f8d8f4), color-stop(#efd9f9), color-stop(#e5dafd), color-stop(#dadbff), color-stop(#dadbff),
  color-stop(#dadbff), color-stop(#dadbff), color-stop(#e5dafd), color-stop(#efd9f9), color-stop(#f8d8f4), to(#ffd8ee));
  background-image: -webkit-linear-gradient(left bottom, #ffd8ee, #f8d8f4, #efd9f9, #e5dafd, #dadbff, #dadbff,
  #dadbff, #dadbff, #e5dafd, #efd9f9, #f8d8f4, #ffd8ee);
  background-image: linear-gradient(to right top, #ffd8ee, #f8d8f4, #efd9f9, #e5dafd, #dadbff, #dadbff,
  #dadbff, #dadbff, #e5dafd, #efd9f9, #f8d8f4, #ffd8ee);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  font-family: 'Sedgwick Ave Display', cursive;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

h1{
  font-size: 5em;
}
p{
  font-size: 30px;
}

.counter{
    display: inline-block;
}

.circle.active{
background-color: white;
border: solid 4px black;
}

.circle{
    width: 200px;
    height: 200px;
    background-color: black;
    border-radius: 50%;
    border: solid 4px white;
    box-shadow: 0 0 3px 2px grey;
    margin: 20px;
    display: inline-block;
}

.smile{
    width: 200px;
    height: auto;
}
.buttons{
    display: inline-block;
    background-color: rgb(129, 94, 175);
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    font-size: 33px;
    font-family: 'Sedgwick Ave Display', cursive;
    margin-left: 10px;
    margin-top: 6%;  
  }


#result{
    position:fixed;
    background-color: rgba(0,0,0,0.7);
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
    display: flex;
    justify-content: center;
    align-items: center;
}

.gameoverbox{
    background-color: white;
    border-radius: 10px;
    width: 400px;
    height: 200px;
    padding: 30px 0 30px 0;
    margin-left: auto;
    margin-right: auto;
    justify-content: center;
    align-items: center;
    position: relative;
}

.gameover{
    font-size: 32px;
}
